<template>
  <div class="order_refund_Rate">
    <div class="left_menu">
      <el-form :inline="true" :model="form" class="demo-form-inline">
        <el-form-item label="渠道">
          <el-select v-model="form.platform">
            <el-option label="全部" value="all"></el-option>
            <el-option
              v-for="(item, i) in channels"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="国家">
          <el-select @change="changeOpt('country')" v-model="form.country">
            <el-option label="全部" value="all"></el-option>
            <el-option
              v-for="(item, i) in countries"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点">
          <el-select v-model="form.site">
            <el-option label="全部" value="all"></el-option>
            <el-option
              v-for="(item, i) in stores"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="品名">
          <el-select v-model="form.product_name">
            <el-option label="全部" value="all"></el-option>
            <el-option
              v-for="(item, i) in product_names"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item> -->

        <el-form-item>
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="right">
      <div class="topOpt">
        <div>
          <label>时间范围</label>
          <el-select @change="search" v-model="form.date">
            <el-option label="全部" value="all"></el-option>
            <el-option
              v-for="(item, i) in timeranges"
              :key="i"
              :label="item + '天内'"
              :value="item"
            ></el-option>
          </el-select>
        </div>
      </div>
      <el-table
        ref="orderreturnTable"
        :data="tableBody"
        style="width: 100%"
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.3)"
      >
        <el-table-column :label="title_" width="100%" align="center">
          <el-table-column
            label="序号"
            type="index"
            :index="indexMethod"
            align="center"
            width="60"
          ></el-table-column>
          <el-table-column
            prop="site"
            label="站点"
            align="center"
            width="100"
          ></el-table-column>
          <el-table-column
            prop="country"
            label="国家"
            align="center"
            width="100"
          ></el-table-column>
          <el-table-column
            prop="name_shop"
            label="店铺"
            align="center"
            width="100"
          ></el-table-column>
          <el-table-column
            prop="product_code"
            label="产品编码"
            align="center"
            width="150"
          ></el-table-column>
          <el-table-column
            prop="product_name"
            label="品名"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="sku"
            label="sku"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="return_sku"
            label="退款订单量"
            align="center"
            width="100"
          ></el-table-column>
          <el-table-column
            prop="ord_sku"
            label="总订单量"
            align="center"
            width="100"
          ></el-table-column>
          <el-table-column label="退款率" align="center" width="100">
            <template slot-scope="scope">
              <span v-if="scope.row.return_sku && scope.row.ord_sku">
                {{
                  (
                    (Number(scope.row.return_sku) / Number(scope.row.ord_sku)) *
                    100
                  ).toFixed(2) + "%"
                }}
              </span>
              <span v-else>
                {{ 0 }}
              </span>
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="changePage"
        :current-page.sync="currentPage"
        :page-size="50"
        layout="total,prev, pager, next,jumper"
        :total="totalNum"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      loading: false,
      currentPage: 1,
      totalNum: 0,
      form: {
        platform: "all",
        site: "all",
        country: "all",
        date: "all"
      },
      channels: [],
      stores: [],
      countries: [],
      timeranges: [15, 30, 60, 90, 150, 365],
      // 表格
      tableBody: [],
      data1: [], //station
      data2: [], // country
      data3: [] // product_name
    };
  },
  computed: {
    title_() {
      return this.title;
    }
  },
  methods: {
    indexMethod(index) {
      return index + 1 + 50 * (this.currentPage - 1);
    },
    changePage(val) {
      this.currentPage = val;
      // 请求
      this.search();
    },
    changeOpt(val) {
      if (val === "country") {
        this.form.station = "all";
        this.stores = [];
        // this.form.product_name = "all";
        // this.product_names = [];
        if (this.form.country !== "all") {
          let url = "server/personnel_management/get_sign_area/";
          this.$axios
            .get(this._url2 + url, {
              params: {
                country: this.form.country
              }
            })
            .then(res => {
              this.stores = res.data.data;
              // this.form.station = this.stores[0];
              // this.changeOpt("store");
            });
        }
      }
    },
    search() {
      // 搜索请求
      // 标题
      console.log("搜索");
      let titleStr = "";
      for (let key in this.form) {
        if (this.form[key] === "all") {
          continue;
        }
        titleStr += this.form[key] + "----";
      }

      this.title = titleStr.replace(/-+$/g, "");

      this.loading = true;
      let url = "server/order_management/order_refund/";
      let formData = {};
      for (let key in this.form) {
        let item = this.form[key];
        if (item !== "all") {
          formData[key] = item;
        } else {
          formData[key] = "";
        }
      }
      formData["page"] = this.currentPage;

      this.$axios
        .get(this._url2 + url, {
          params: formData
        })
        .then(res => {
          this.tableBody = res.data.re_data;
          this.totalNum = 0;
          if (
            res.data.count_data.length > 0 &&
            res.data.count_data[0].count_data
          ) {
            this.totalNum = res.data.count_data[0].count_data;
          }

          // orderreturnTable

          this.$nextTick(() => {
            this.$refs["orderreturnTable"].bodyWrapper.scrollTop = 0;
          });
          this.loading = false;
        });
    },
    getOption() {
      let url1 = "server/personnel_management/get_sign_country/";
      let p1 = this.$axios.get(this._url2 + url1);
      p1.then(res => {
        this.channels.push(res.data.data1);
        this.form.platform = this.channels[0];
        this.countries = res.data.data;
        this.form.country = this.countries[0];
      }).then(value => {
        let url = "server/personnel_management/get_sign_area/";
        this.$axios
          .get(this._url2 + url, {
            params: {
              country: this.form.country
            }
          })
          .then(res => {
            this.stores = res.data.data;
            this.form.site = this.stores[0];
            // this.changeOpt("store");
            this.form.date = this.timeranges[0];

            this.search();
          });
      });
    }
  },
  created() {
    this.getOption();
  }
};
</script>

<style scoped>
.order_refund_Rate {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding-top: 50px;
}
.left_menu {
  display: flex;
  flex-direction: column;
  flex: 0 0 320px;
  text-align: right;
  background: rgba(255, 255, 255, 0.9);
  padding: 5px;
  border-radius: 5px;
}
.left_menu >>> .el-form-item__label {
  color: #000;
}
.left_menu >>> .el-button.el-button--primary {
  width: 200px;
  margin-left: -22px;
}
.right {
  margin-left: 30px;
  border-left: 1px solid;
  padding: 0 100px;
  width: 100%;
}
</style>
<style>
.order_refund_Rate .right .el-pagination {
  background: #fff;
}
</style>



